html {
  font-size: 16px;
}

html,
body {
  height: 100%;
}

body {
  font-family: 'Open Sans', Arial, Helvetica, sans-serif;
  line-height: 1.42;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  color: #464E62;
}

.wrapper {
  position: relative;
  width: 100%;
  min-height: 40.625rem;
  overflow: hidden;
}

.container {
  width: 95%;
  max-width: 1097px;
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
}

.tabel {
  border-collapse: collapse;
}

ul {
  padding: 0;
  margin: 0;
}

ul li {
  list-style: none;
}

p {
  padding: 0;
  margin: 0;
}

footer {
  position: relative;
  width: 100%;
}

* {
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  display: block;
  max-width: 100%;
}

input {
  outline: none;
}

.section-welcome {
  background: url("../img/background/hero.jpg") center top/cover no-repeat;
}

.container__hero {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-bottom: 10.9375rem;
}

.main-content {
  display: flex;
  justify-content: center;
}

.main-content span {
  font-weight: 600;
  font-size: 1.125rem;
  line-height: 1.875rem;
  margin-right: 2.125rem;
  color: rgba(70, 78, 98, 0.5);
}

.main-content__user {
  font-weight: 800;
  font-size: 4.5rem;
  line-height: 3.75rem;
  color: #464E62;
  margin-bottom: 0;
}

.scroll {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 0.9375rem;
}

.scroll span {
  margin-right: 0.9375rem;
  font-weight: 600;
  font-size: 1.125rem;
  line-height: 1.875rem;
}

.scroll__icon {
  width: 0.875rem;
  height: 0.875rem;
  position: relative;
  top: -0.75rem;
  animation-name: scroll;
  animation-duration: 1s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-delay: 1s;
}

@keyframes scroll {
  0% {
    top: -0.75rem;
  }

  50% {
    top: 0rem;
  }

  100% {
    top: 1.0625rem;
  }
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-scroll {
  background-color: #252830;
}

.container-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 40px;
  width: 100%;
}

.header__user {
  font-weight: 800;
  font-size: 2.25rem;
  line-height: 3.0625rem;
  transition: .3s all;
}

.header__user:hover {
  color: #5500F2;
}

.navigation__list {
  display: flex;
  justify-content: center;
  align-items: center;
}

.navigation__item {
  margin-right: 3.75rem;
  font-weight: 600;
  transition: .3s all;
  z-index: 10000;
}

.navigation__item:last-child {
  margin-right: 0;
}

.navigation__link {
  transition: .3s all;
}

.navigation__link:hover {
  color: #5500F2;
  font-weight: 600;
  text-decoration: underline;
}

.socials__list {
  display: flex;
  justify-content: center;
  align-items: center;
}

.socials__item {
  margin-right: 2.1875rem;
}

.socials__item:last-child {
  margin-right: 0;
}

.socials__link {
  width: 1.5625rem;
  height: 1.5625rem;
  display: block;
  fill: #464E62;
  transition: .3s all;
}

.socials__link:hover {
  fill: #5500F2;
}

.socials__icon {
  width: 100%;
  height: 100%;
}

.container__about {
  display: flex;
  justify-content: space-between;
}

.about {
  padding-bottom: 14.375rem;
}

.about__user {
  width: 40%;
}

.user-img {
  margin-bottom: 4.5rem;
  padding-left: 4.0625rem;
}

.user-img__style {
  width: 12.5rem;
  height: 12.5rem;
  border-radius: 3.125rem;
}

.user-icon {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  box-shadow: 0rem 0.0625rem 4.5625rem -0.625rem #464E62;
}

td {
  padding-right: 5.5625rem;
  padding-bottom: 0.75rem;
}

td:last-child {
  padding-right: 0;
}

.td-info {
  color: rgba(70, 78, 98, 0.5);
}

.td-desc {
  font-weight: 600;
  line-height: 1.875rem;
}

.about__data {
  width: 60%;
  padding-left: 4.25rem;
}

.about__desc {
  color: rgba(70, 78, 98, 0.8);
  margin-bottom: 1.3125rem;
  line-height: 1.375rem;
}

.about__title {
  font-weight: bold;
  font-size: 2.25rem;
  line-height: 3.0625rem;
  margin-bottom: 1rem;
}

.about__text {
  font-weight: 600;
  line-height: 1.875rem;
  color: rgba(70, 78, 98, 0.8);
  margin-bottom: 5.75rem;
  position: relative;
}

.star {
  width: 1.6875rem;
  height: 3.1875rem;
  position: absolute;
  top: 0;
  left: -3.125rem;
}

.about__extra {
  display: flex;
  position: relative;
}

.about__extra-content {
  font-style: italic;
  line-height: 1.875rem;
  color: rgba(70, 78, 98, 0.8);
}

.skills {
  position: relative;
  padding-top: 5.0625rem;
  margin-bottom: 17.0625rem;
}

.skills::before {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  height: 75%;
  width: 45%;
  background: url("../img/background/Рюкзак.png") left bottom/contain no-repeat;
}

.bag {
  display: none;
  width: 50%;
  height: 80%;
}

.skills__title {
  font-weight: bold;
  font-size: 1.875rem;
  line-height: 2.5625rem;
  margin-bottom: 2.4375rem;
  display: flex;
  justify-content: center;
}

.skills__text {
  display: flex;
  font-weight: 600;
  margin-bottom: 5.8125rem;
  line-height: 1.875rem;
  width: 79%;
  padding-left: 22%;
  justify-content: center;
  align-items: center;
}

.skills_category-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.skills__category {
  display: flex;
  flex-direction: column;
  padding-left: 30%;
}

.skills-row {
  display: flex;
  flex-wrap: wrap;
}

.skills-row__item {
  margin-right: 0.75rem;
}

.skills-row__item:last-child {
  margin-right: 0;
}

.skills__item {
  font-weight: bold;
  font-size: 1.5rem;
  line-height: 2.0625rem;
  margin-bottom: 2.5rem;
}

.skills__item span {
  display: block;
  margin-bottom: 1.5625rem;
}

.skills__item:last-child {
  margin-bottom: 0;
}

.skill {
  position: relative;
  width: 10.375rem;
  height: 10.375rem;
}

.skill__title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-weight: bold;
  font-size: 1.125rem;
  line-height: 1.5625rem;
  color: rgba(70, 78, 98, 0.5);
}

.skill-circle {
  stroke-dasharray: 251;
  fill: transparent;
}

.skill-circle--under {
  stroke: #eaeaea;
  stroke-width: 0.3125rem;
}

.skill-circle--above {
  stroke-width: 0.75rem;
  stroke-dashoffset: 12.5rem;
}

.section-tittle {
  font-weight: bold;
  font-size: 2.25rem;
  line-height: 3.0625rem;
}

.works__preview {
  position: relative;
  width: 58%;
  height: 100%;
}

.works__preview-img {
  object-fit: cover;
  width: 39.8125rem;
  height: 22.5rem;
}

.works__list {
  display: flex;
  flex-direction: column;
  position: absolute;
  bottom: 0;
  right: 0;
  z-index: 1000;
}

.works__item {
  width: 7.25rem;
  height: 4.0625rem;
}

.works__link {
  width: 100%;
  height: 100%;
  display: block;
}

.works__link-prev {
  width: 100%;
  height: 100%;
}

.works__link:hover {
  border: 0.125rem solid #DC9322;
  transform: scale(1.05);
}

.active-preview {
  border: 0.125rem solid #DC9322;
  transform: scale(1.05);
}

.works__controls {
  display: flex;
  position: absolute;
  top: 100%;
  right: 0;
}

.button-works {
  outline: none;
  width: 2.1875rem;
  height: 2.1875rem;
  background-color: #333C49;
  fill: #464E62;
  appearance: none;
  border: 0;
  border-radius: 0;
  padding: 0;
  display: block;
  transition: .3s all;
}

.button-works:hover {
  fill: #717883;
  cursor: pointer;
  background-color: #464E62;
}

.btn-icon {
  width: 0.75rem;
  height: 0.75rem;
}

.btn-icon--prev {
  transform: rotate(180deg);
}

.works__desc-list {
  display: flex;
  flex: 1;
  transition-duration: .8s;
  transition-property: transform;
  transition-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.works__content {
  display: flex;
  justify-content: space-between;
  position: relative;
  padding-bottom: 4rem;
}

.works__content::before {
  content: attr(data-slide);
  position: absolute;
  top: -5%;
  line-height: 1;
  font-size: 25rem;
  right: 0;
  font-weight: 800;
  color: #E6E6E6;
  transform: translateX(-137%);
}

.slider-works {
  max-width: 27.6875rem;
  overflow: hidden;
  width: 50%;
}

.works__desc {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  z-index: 1000;
  padding-left: 20px;
  flex: 0 0 100%;
}

.tags {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2.1875rem;
}

.tags__item {
  font-weight: 600;
  font-size: 0.9375rem;
  line-height: 1.25rem;
  color: rgba(51, 60, 73, 0.8);
  padding: 0.875rem 1.375rem;
  background: #F3F6F9;
  border-radius: 2.5rem;
  margin-right: 0.5rem;
}

.tags__item:last-child {
  margin-right: 0;
}

.works__tittle {
  font-weight: bold;
  font-size: 1.875rem;
  line-height: 2.5625rem;
  margin-bottom: 2.1875rem;
}

.works__text {
  font-weight: 600;
  color: rgba(51, 60, 73, 0.8);
  margin-bottom: 2.875rem;
}

.more {
  display: flex;
  justify-content: center;
  text-decoration-color: #464E62;
  fill: #464E62;
  color: #464E62;
}

.more:hover {
  text-decoration: underline;
  color: #5500F2;
  fill: #5500F2;
}

.more__icon {
  width: 1.375rem;
  height: 1.375rem;
  margin-right: 1rem;
}

.more__text {
  font-weight: bold;
  font-size: 1.125rem;
  line-height: 1.5625rem;
}

.section-reviews {
  background: url("../img/background/budda.jpg") top right 70% no-repeat;
}

.slider__header {
  padding-top: 14.0625rem;
  display: flex;
  justify-content: space-between;
  margin-bottom: 4.875rem;
}

.slider__controls {
  padding-right: 20%;
  display: flex;
}

.button-slider {
  width: 3.3125rem;
  height: 3.3125rem;
  background-color: transparent;
  border-radius: 50%;
  border: 0.125rem solid #464E62;
  outline: none;
  margin-right: 1.75rem;
  cursor: pointer;
  fill: #464E62;
  transition: .3s all;
}

.button-slider:last-child {
  margin-right: 0;
}

.button-slider:hover {
  border-color: #5500F2;
}

.slider-btn {
  width: 100%;
  height: 100%;
}

.slider__list {
  padding-bottom: 12.125rem;
  min-width: 0;
}

.slider__list--phones {
  display: none;
}

.reviews__list {
  display: flex;
}

.reviews__desc {
  padding-left: 18%;
  min-height: 14.125rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.reviews__item {
  flex: 1;
  position: relative;
}

.reviews__item::before {
  content: '';
  display: block;
  position: absolute;
  background: url("../img/quote/quote.png") center center/contain no-repeat;
  fill: red;
  top: 0;
  left: 0;
  width: 13%;
  height: 21%;
}

.reviews__text {
  font-weight: 600;
  line-height: 1.875rem;
  color: rgba(70, 78, 98, 0.8);
  display: block;
  max-width: 20.625rem;
  margin-bottom: 0.625rem;
}

.reviews__user {
  display: flex;
}

.reviews__user-img {
  width: 4.6875rem;
  height: 4.6875rem;
  border-radius: 50%;
  background-position: top 10% center;
  background-repeat: no-repeat;
  margin-right: 1.9375rem;
}

.reviews__user-img--1 {
  background-image: url("../img/quote/1.png");
}

.reviews__user-img--2 {
  background-image: url("../img/quote/2.png");
  background-position: top center;
}

.reviews__user-desc {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.reviews__user-desc span {
  font-weight: 600;
  line-height: 1.375rem;
  color: rgba(70, 78, 98, 0.5);
}

.user-name {
  font-weight: bold;
  font-size: 1.125rem;
  line-height: 1.5625rem;
}

.footer {
  background: #252830;
  padding: 3.9375rem 0rem;
}

.container-footer {
  display: flex;
}

.footer__copyright {
  display: flex;
  flex-direction: column;
  width: 27%;
}

.footer__copyright-year {
  font-weight: 600;
  font-size: 3rem;
  line-height: 4.0625rem;
  color: rgba(70, 78, 98, 0.5);
  margin-bottom: 1.5rem;
}

.footer__copyright-name {
  font-weight: bold;
  font-size: 1.125rem;
  line-height: 1.875rem;
  color: #FFFFFF;
  margin-bottom: 1.125rem;
}

.footer__copyright-text {
  line-height: 1.875rem;
  color: rgba(70, 78, 98, 0.5);
}

.footer__data {
  padding-top: 1.25rem;
  width: 73%;
}

.footer__data-content {
  display: flex;
}

.footer__data-col {
  margin-top: 2.9375rem;
  margin-right: 1.5625rem;
  width: 100%;
}

.footer__data-text {
  display: block;
  line-height: 1.875rem;
  color: rgba(255, 255, 255, 0.45);
  margin-bottom: 1.75rem;
  max-width: 21.3125rem;
}

.footer__data-text--quote {
  max-width: 28.5625rem;
  display: block;
}

.footer__socials {
  display: flex;
}

.footer__socials-item {
  width: 3.125rem;
  height: 3.125rem;
  border: 0.0625rem solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  margin-right: 1.3125rem;
  transition: .3s all;
}

.footer__socials-item:last-child {
  margin-right: 0;
}

.footer__socials-item:hover {
  border-color: #5500F2;
}

.footer__socials-link {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  fill: rgba(255, 255, 255, 0.3);
  transition: .3s all;
