.burger {
  width: 20px;
  height: 20px;
  position: relative;
  display: none;
  cursor: pointer;
  position: fixed;
  right: 0.9375rem;
  z-index: 2000;
}

.burger__line {
  display: block;
  width: 100%;
  height: 4px;
  background-color: #464E62;
  position: absolute;
  left: 0;
}

.burger__line--first {
  top: 0;
}

.burger__line--second {
  top: 50%;
  transform: translateY(-50%);
}

.burger__line--third {
  bottom: 0;
}

.navigation__close {
  width: 1.5rem;
  height: 1.5rem;
  position: absolute;
  top: 3.125rem;
  right: 1.25rem;
  z-index: 10000;
  cursor: pointer;
  display: none;
}

.navigation__close-line {
