.swiper-pagination {
    padding-bottom: rem(150px);

    @include phones() {
        padding-bottom: 2%;
    }
}

.swiper-pagination-bullet {
    width: rem(10px);
    height: rem(10px);
    opacity: .1;

    &-active {
        background-color: #5500F2;
        opacity: .7;
    }
}